/**
 * @memberof Stack
 * @instance
 * @return {number[]}
 */
export default function min() {
  this.checkProcessable('min', {
    bitDepth: [8, 16]
  });
  var min = this[0].min;

  for (var i = 1; i < this.length; i++) {
    for (var j = 0; j < min.length; j++) {
      min[j] = Math.min(min[j], this[i].min[j]);
    }
  }

  return min;
}