import { median as medianFromHistogram } from '../../util/histogram';
/**
 * @memberof Stack
 * @instance
 * @return {number[]}
 */

export default function median() {
  this.checkProcessable('median', {
    bitDepth: [8, 16]
  });
  var histograms = this.getHistograms({
    maxSlots: this[0].maxValue + 1
  });
  var result = new Array(histograms.length);

  for (var c = 0; c < histograms.length; c++) {
    var histogram = histograms[c];
    result[c] = medianFromHistogram(histogram);
  }

  return result;
}