import { SVD } from 'ml-matrix';
export default function getSeparatedKernel(kernel) {
  var svd = new SVD(kernel, {
    autoTranspose: true
  });
  if (svd.rank !== 1) return null;
  var s = Math.sqrt(svd.s[0]);
  var v = svd.U.to2DArray().map(function (v) {
    return v[0] * s;
  });
  var h = svd.V.to2DArray().map(function (h) {
    return h[0] * s;
  });
  return [v, h];
}