export default function Matrix(width, height, defaultValue) {
  var matrix = new Array(width);

  for (var x = 0; x < width; x++) {
    matrix[x] = new Array(height);
  }

  if (defaultValue) {
    for (var _x = 0; _x < width; _x++) {
      for (var y = 0; y < height; y++) {
        matrix[_x][y] = defaultValue;
      }
    }
  }

  matrix.width = width;
  matrix.height = height;
  Object.setPrototypeOf(matrix, Matrix.prototype);
  return matrix;
}

Matrix.prototype.localMin = function (x, y) {
  var min = this[x][y];
  var position = [x, y];

  for (var i = Math.max(0, x - 1); i < Math.min(this.length, x + 2); i++) {
    for (var j = Math.max(0, y - 1); j < Math.min(this[0].length, y + 2); j++) {
      if (this[i][j] < min) {
        min = this[i][j];
        position = [i, j];
      }
    }
  }

  return {
    position: position,
    value: min
  };
};

Matrix.prototype.localMax = function (x, y) {
  var max = this[x][y];
  var position = [x, y];

  for (var i = Math.max(0, x - 1); i < Math.min(this.length, x + 2); i++) {
    for (var j = Math.max(0, y - 1); j < Math.min(this[0].length, y + 2); j++) {
      if (this[i][j] > max) {
        max = this[i][j];
        position = [i, j];
      }
    }
  }

  return {
    position: position,
    value: max
  };
};

Matrix.prototype.localSearch = function (x, y, value) {
  var results = [];

  for (var i = Math.max(0, x - 1); i < Math.min(this.length, x + 2); i++) {
    for (var j = Math.max(0, y - 1); j < Math.min(this[0].length, y + 2); j++) {
      if (this[i][j] === value) {
        results.push([i, j]);
      }
    }
  }

  return results;
};