/**
 * @memberof Stack
 * @instance
 * @return {number[]}
 */
export default function max() {
  this.checkProcessable('min', {
    bitDepth: [8, 16]
  });
  var max = this[0].max;

  for (var i = 1; i < this.length; i++) {
    for (var j = 0; j < max.length; j++) {
      max[j] = Math.max(max[j], this[i].max[j]);
    }
  }

  return max;
}